.order-form {
  position: relative;
  @extend .flex-column;

  width: 100%;
  max-width: 1120px;
  height: 100%;
  min-height: 415px;
  padding: 50px;
  padding-right: 400px;
  margin: 0 auto;
  background-color: var(--main-bg-color);

  &::after {
    position: absolute;
    top: 0;
    right: 0;

    width: 345px;
    height: 100%;

    content: '';

    background-image: url("../assets/img/woman@1x.jpg");
  }

  @include m-b-tablet-down {
    padding: 30px;
  }

  @include m-b-mobile-lg-down {
    padding: 20px;
  }

  h2 {
    margin-bottom: 10px;
  }

  p {
    line-height: 26px;

    margin-bottom: 30px;
    color: var(--secondary);
  }

  form {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: auto;
    grid-column-gap: 20px;
    grid-row-gap: 20px;

    margin-bottom: 30px;

    fieldset {
      @extend .fieldset-default;
    }
  }

  &__btn {
    margin-top: auto;
  }
}
